"use client"

import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Sparkles, Menu, ChevronDown } from "lucide-react"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"

export default function Navigation() {
  const navItems = [
    { name: "Home", href: "/" },
    { name: "Methodology", href: "/methodology" },
    { name: "Models", href: "/models" },
    { name: "Analysis", href: "/upload" },
  ]

  return (
    <nav className="sticky top-0 z-50 border-b border-white/5 bg-black/20 backdrop-blur-xl transition-all duration-300">
      <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-between h-20">
          {/* Logo */}
          <Link href="/" className="flex items-center gap-3 group z-50">
            <div className="w-10 h-10 bg-white/5 rounded-xl flex items-center justify-center border border-white/10 group-hover:bg-primary/20 transition-all duration-500 shadow-lg">
              <Sparkles className="w-6 h-6 text-primary" />
            </div>
            <span className="bg-gradient-to-r from-primary to-secondary bg-clip-text text-transparent font-extrabold text-xl tracking-tight whitespace-nowrap">
              BE Spectra
            </span>
          </Link>

          {/* Universal Navigation Dropdown */}
          <div className="flex z-50">
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button
                  variant="outline"
                  size="sm"
                  className="text-white border-white/10 bg-white/5 hover:bg-white/10 gap-2"
                >
                  <span className="text-sm font-medium">Navigation</span>
                  <ChevronDown className="w-4 h-4 text-white/50" />
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent align="end" className="z-[100] w-[200px] bg-black/90 backdrop-blur-xl border-white/10 text-white">
                {navItems.map((item) => (
                  <DropdownMenuItem key={item.name} asChild>
                    <Link
                      href={item.href}
                      className="w-full cursor-pointer text-white/70 hover:text-primary focus:text-primary focus:bg-white/5"
                    >
                      {item.name}
                    </Link>
                  </DropdownMenuItem>
                ))}
                <div className="p-2 mt-2 border-t border-white/5">
                  <Link href="/upload" className="w-full">
                    <Button
                      size="sm"
                      className="w-full bg-primary hover:bg-primary/80 text-primary-foreground font-bold"
                    >
                      Get Started
                    </Button>
                  </Link>
                </div>
              </DropdownMenuContent>
            </DropdownMenu>
          </div>
        </div>
      </div>
    </nav>
  )
}
